@import url("https://fonts.googleapis.com/css2?family=Rubik+Mono+One&display=swap");

html {
  height: 100%;
}

body {
  min-width: 320px;
  height: 100%;
  margin: 0;
  font-size: 30px;
}

.visually-hidden {
  position: absolute;
  width: 0;
  height: 0;
  margin: 0;
  padding: 0;
  overflow: hidden;
  border: none;
  outline: 0;
}

.text-gradient {
  width: fit-content;
  font-family: "Rubik Mono One", sans-serif;
  font-size: 30px;
  margin: 0;
  background-image: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  background-clip: text;
  color: transparent;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  filter: drop-shadow(2px 2px 8px rgba(0, 0, 0, 0.45));
}